import React, { Component } from 'react';
import { Form, Input, Button, message } from 'antd'
import {putProgramDetail} from '/api'
import Upload from '@/components/common/upload'
const FormItem = Form.Item
class editForm extends Component{
  constructor (props) {
    super(props)
    this.state = {
      fileList: this.props.editData.attachments
    }
    this.Submit = this.Submit.bind(this)
    this.getChildrenFileList =this.getChildrenFileList.bind(this)
  }

  Submit (e) {
    e.preventDefault()
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let nodes = []
        this.props.editData.nodes.forEach((v) => {
          nodes.push(v.id)
        })
        const obj = {
          ...values,
          attachments: this.state.fileList,
          nodes
        }
        this.props.programCancel()
        putProgramDetail(this.props.editData.id, obj).then((res) => {
          message.success('')
          this.props.getProgramFuc()
        })
      }
    });
  }

  getChildrenFileList (fileList) {
    this.setState(() => ({
      fileList
    }))
  }

  render() {
    const { getFieldDecorator } = this.props.form
    return(
      <Form onSubmit={this.Submit}>
        <FormItem label={'    '}>
          {
            getFieldDecorator('keywords', {
              rules: [{required: true, message: '!'}],
              initialValue: this.props.editData.keywords?this.props.editData.keywords.join(' '): ''
            })(
              <Input.TextArea placeholder={''}/>
            )
          }
        </FormItem>
        <FormItem label={''}>
          {
            getFieldDecorator('content', {
              rules: [{required: true, message: '!'}],
              initialValue: this.props.editData.content
            })(
              <Input.TextArea placeholder={''}/>
            )
          }
        </FormItem>
        <FormItem label={''}>
        <Upload
          fileList={this.state.fileList}
          getFileList={this.getChildrenFileList}
          ref={(upload) => this.uploadComponent = upload}
          isEdit={true}
        />
        </FormItem>
        <FormItem>
          <Button htmlType='submit'></Button>
        </FormItem>
      </Form>
    )
  }
}
export default Form.create()(editForm)
