/* eslint-disable */
import axios from 'axios'
import store from '@/store';
import * as C from '@/store/constants'
import {
  httpRequestList
} from './httpRequestList'
import finalUrl from './finalUrl'
import {errorFuc} from './httpError'
// 
axios.interceptors.request.use(config => {
  if (sessionStorage.Token) config.headers.Authorization = `Bearer ${sessionStorage.Token}`
  return config
}, error => Promise.reject(error))
// 
axios.interceptors.response.use(response => {
  if (response.status === 200 || response.status === 304) return response
},(error) => {
  closeLoading()
  return Promise.reject(errorFuc(error))
})

const opt = (options) => {
  const CancelToken = axios.CancelToken
  return axios({
    ...options,
    timeout: 15000,
    cancelToken: new CancelToken((c) => {
      httpRequestList.push(c)
    })
  })
}
/**
 * axios,finalUrlurl  
 * true + '/teamwrok',false'/teamwork' true
 */

