import React, { Component } from 'react';
import { Button, Form, Input, InputNumber, message } from 'antd'
import {postDefaultPhases} from '/api'
const FormItem = Form.Item
class addForm extends Component {
  constructor(props) {
    super(props)
    this.phaseSubmit = this.phaseSubmit.bind(this)
  }

  phaseSubmit (e) {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        postDefaultPhases(values).then(() => {
          message.success('')
          this.props.handleCancel()
          this.props.form.resetFields()
          this.props.getDefaultPhasesFuc()
        })
      }
    })
  }

  render() {
    const {getFieldDecorator} = this.props.form
      return(
      <Form onSubmit={this.phaseSubmit}>
        <FormItem label={''}>
          {getFieldDecorator('name', {
            rules: [{ required: true, message: '!' }]
          })(
            <Input/>
          )}
        </FormItem>
        <FormItem label={''}>
          {getFieldDecorator('orderNumber', {
            rules: [{ required: true, message: '!' }]
          })(
            <InputNumber min={1}/>
          )}
        </FormItem>
        <Button htmlType='submit' type="primary"></Button>
      </Form>
    )
  }
}
export default Form.create()(addForm)
