import React, { Component } from 'react'
import { formDate } from '@/utils/util/common'
import './taskList.scss'

export default class ProjectTask extends Component {
  constructor(props) {
    super(props)
    this.state = {
      dataitem: {
        content: '',
        status: 'CREATED',
        priority: 1
      }
    }
    this.status = {
      CREATED: '',
      CLOSED: '',
      COMPLETED: ''
    }
    // 
    this.taskDetail = this.taskDetail.bind(this)
    this.projectId = this.props.projectId
  }
  // 
  taskDetail(id) {
    this.props.history.push(`/home/taskDetail/${this.projectId}/${id}`)
  }
  render() {
    const { taskList } = this.props
    return (
      <div className="project_task_list_wrapper">
        <div className="taskitem_wrapper">
          <div className="task_tag">
            <label htmlFor="" className={`status_${taskList.status}`}>
              {this.status[taskList.status]}
              <i className="iconfont icon-icon"></i>
            </label>
            <label htmlFor="" className="right">{`${taskList.priority}`}</label>
          </div>
          <div className="taskitem_box">
            <p className="title" onClick={() => {this.taskDetail(taskList.id)}}>{taskList.content}</p>
            <p className="taskitem_status">
              <label htmlFor=""><label htmlFor="" className="status_name"></label>{formDate(taskList.deadline)}</label>
              <label htmlFor=""><label htmlFor="" className="status_name"></label>{taskList.participantCount}</label>
              <label htmlFor=""><label htmlFor=""  className="status_name"></label>{taskList.participantCompletedCount}</label>
            </p>
          </div>
        </div>
      </div>
    )
  }
}
