import React, { Component } from 'react';
import { Table, Form, Row, Col, Input, Button, Select, Modal, message, Radio, Checkbox } from 'antd';
import { connect } from 'react-redux';
import { openLoading, closeLoading, getUserArr, deleteUser, getDepartments, getProductLines, postAddUser, putEmployees } from '@/utils/api'
import './index.scss';

const FormItem = Form.Item;
const Option = Select.Option;
const confirm = Modal.confirm;
const RadioGroup = Radio.Group;
const CheckboxGroup = Checkbox.Group;
// 
// 
const tableState = {
  bordered: true,
  pagination: true,
  showHeader: true,
  size: "middle",
  rowKey: "id"
}
class UserList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      employees: [], // 
      userDep: [], // 
      prodLineList: [], // 
      userVisible: false, // 
      disabled: false, // 
      isEdit: false, // 
      role: 'COORDINATOR', // 
      editItem: {}, // 
      params: {
        page: 0,
        size: 10
      },
      pagination: { // 
        total: 0,
        current: 0,
        showSizeChanger: true,
        showQuickJumper: true,
        onChange: (page, pageSize) => this.pageChange(page, pageSize),
        onShowSizeChange: (current, size) => this.sizeChange(current, size)
      },
      columns: [ // 
        {
          dataIndex: 'role',
          title: '',
          render: (val) => {
            return <label>{roleStatus[val]}</label>
          },
        },
        {
          dataIndex: 'deptName',
          title: '',
        },
        {
          dataIndex: 'name',
          title: '',
        },
        {
          dataIndex: 'sex',
          title: '',
          render: (val) => {
            return <label>{sex[val]}</label>
          },
        },
        {
          dataIndex: 'phone',
          title: '',
        },
        {
          dataIndex: 'email',
          title: '',
          render: (val) => {
            return <label>{val === '' || !val ? '--' :  val}</label>
          },
        },
        {
          title: '',
          render: (item) => {
            return <p><label className='edit_user operate' onClick={() => this.editUser(item)}></label> <label className='del_user operate' onClick={() => this.deleteUser(item.id)}></label></p>
          },
        },
      ]
    }
    this.selectData = {
      roles: '',
      deptId: '',
      name: ''
    }
    // 
    const roleStatus = {
      COORDINATOR:'',
      SERVANT:'',
    }
    const sex = ['', '']
    
    this.handleSearch = this.handleSearch.bind(this)
    this.roleChange = this.roleChange.bind(this)
    this.depChange = this.depChange.bind(this)
    this.keywordsChange = this.keywordsChange.bind(this)
    this.deleteUser = this.deleteUser.bind(this)
    // 
    this.handleCancel = this.handleCancel.bind(this)
    this.handleConfirm = this.handleConfirm.bind(this)
    this.showAddUserModal = this.showAddUserModal.bind(this)
    this.userRoleChange = this.userRoleChange.bind(this) // 
    // 
    this.editUser = this.editUser.bind(this)
  }
  componentDidMount() {
    this.getUser()
    this.queryDep() // 
    this.queryProductLine()
  }
  // 
  keywordsChange(e) {
    this.selectData.name = e.target.value
  }
  // 
  getUser (params) {
    openLoading()
    const datas = {
      paging: true,
      ...this.selectData,
      ...this.state.params,
      ...params
    }
    getUserArr(datas).then((res) => {
      closeLoading()
      if (!res) return
      const { content, totalElements} = res.data
      let pagination = Object.assign({}, this.state.pagination, {total: totalElements})
      this.setState(() => ({pagination}))
      this.setState(() => ({employees: content}))
    })
  }
  // 
  queryDep() {
    getDepartments().then(res => {
      if (!res) return
      const userDep = res.data
      this.setState(() => ({userDep}))
    })
  }
  // 
  queryProductLine() {
    getProductLines().then(res => {
      if (!res) return
      let prodLineList = res.data
      let prodLineArr = (val) => {
        let prodLineName = []
        prodLineList.forEach(item => {
          if (val.includes(item.id)) {
            prodLineName.push(item.abbreviation)
          }
        })
        return prodLineName
      }
      let prodLine = {
        dataIndex: 'productLineIds',
        title: '',
        render: (val) => {
          return <label>{val.length === 0 ? '--' :  prodLineArr(val).toString()}</label>
        },
      }
      let columns = this.state.columns
      columns.splice(2,0, prodLine)
      this.setState(() => ({prodLineList, columns}))
    })
  }
  // 
  deleteUser (id) {
    confirm({
      title: '?',
      okText: '',
      cancelText: '',
      onOk: () => {
        this.delFun(id)
      }
    });
  }
  delFun(id) {
    deleteUser(id).then((res) => {
      if (res.status === 200) {
        message.info('', 1)
        this.getUser()
      }
    })
  }
  // 
  pageChange(page, pageSize) {
    let params = {
      size: pageSize,
      page: page -1
    }
    this.setPagination(page)
    this.getUser(params)
  }
  sizeChange(current, size) {
    let params = {
      size,
      page: current - 1
    }
    let datas = Object.assign({}, this.state.params, { size: size })
    this.setState(() => {
      return {params: datas}
    })
    this.getUser(params)
  }
  // 
  handleSearch() {
    let params = {
      page: 0,
      ...this.selectData
    }
    this.setPagination()
    this.getUser(params)
  }
  // 
  setPagination(current = 0) {
    let pagination = Object.assign({}, this.state.pagination, {current})
    this.setState(() => ({pagination}))
  }
  // 
  roleChange(val) {
    this.selectData.roles = val ? val : '';
  }
   // 
  depChange(val) {
    this.selectData.deptId = val ? val : '';
  }
  // 
  // 
  handleCancel() {
    this.clearForm()
    this.setState(() => ({userVisible: false}))
  }
  // 
  userRoleChange(val) {
    let role = val ? val : '';
    this.setState(() => ({role }))
  }
  // 
  handleConfirm(e) {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (!this.state.isEdit) {
          this.postUserInfo(values)
          return
        }
        this.editUserFunc(values)
      }
    });
  }
  // 
  validateTel(rule, value, callback) {
    let reg = /^[1][3,4,5,7,8,9][0-9]{9}$/
    if (!reg.test(value)) { // 
      callback('11'); 
    } else {
      callback()
    }
  }
  // 
  validateEmail(rule, value, callback) {
    const reg = /^[A-z0-9_]{3,12}@[A-z0-9_]{2,12}(\.com|\.cn|\.com\.cn)$/g
    if (value === '' || !value) {
      callback()
    } else if (!reg.test(value)) { // 
      callback(''); 
    } else {
      callback()
    }
  }
  // fun
  postUserInfo (formData) {
    openLoading()
    this.setState(() => ({disabled: true}))
    postAddUser(formData).then((res) => {
      closeLoading()
      this.setState(() => ({disabled: false}))
      if (res.status === 200) {
        message.info('')
        this.clearForm()
        this.setState(() => ({userVisible: false}))
        this.getUser()
      }
    }).catch(() => {
      this.setState(() => ({disabled: false}))
    })
  }
  // func
  editUserFunc(params) {
    openLoading()
    putEmployees(this.state.editItem.id, params).then((res) => {
      closeLoading()
      if (res.status === 200) {
        message.info('')
        this.clearForm()
        this.setState(() => ({userVisible: false}))
        this.getUser()
      }
    })
  }
  // 
  clearForm() {
    this.setState(() => ({isEdit: false}))
    this.props.form.resetFields()
  }
  // 
  editUser(item) {
    this.setState(() => ({isEdit: true, editItem: item, userVisible: true, role: item.role}))
  }
  // &
  addUserRender(isEdit = false) {
    const title = isEdit ? '' : ''
    const { getFieldDecorator } = this.props.form;
    let { userDep, prodLineList, disabled, editItem, role } = this.state
    return (
      <Modal title={title}
        visible={this.state.userVisible}
        footer={null}
        className='add_user_modal'
        onCancel={this.handleCancel}
      >
        <Form
            layout='inline'
            className="ant-advanced-search-form"
            onSubmit={this.handleConfirm}
          >
          <FormItem label=''>
            {
              !isEdit ?(getFieldDecorator('sex', {
                rules: [{
                  required: true,
                  message: '',
                }],
                initialValue: 1
              })(
                <RadioGroup>
                  <Radio value={1}></Radio>
                  <Radio value={0}></Radio>
                </RadioGroup>
              )) : (<RadioGroup value={editItem.sex}>
                <Radio value={1} disabled></Radio>
                <Radio value={0} disabled></Radio>
              </RadioGroup>)
              }
          </FormItem>
          <FormItem label=''>
            {getFieldDecorator('role', {
              rules: [{
                required: true,
                message: '',
              }],
              initialValue: isEdit ? editItem.role: ''
            })(
              <Select placeholder="" style={{ width: 280 }} onChange={this.userRoleChange}>
                <Option value="COORDINATOR"></Option>
                <Option value="SERVANT"></Option>
              </Select>
            )}
          </FormItem>
          {
            role !== 'COORDINATOR' ? '' : (
              <FormItem label='' style={{ width: '100%'}}>
                {getFieldDecorator('productLineIds', {
                  rules: [{
                    required: true,
                    message: '',
                  }],
                  initialValue: isEdit ? editItem.productLineIds: []
                })(
                  <CheckboxGroup style={{ width: '100%',lineHeight: '32px'}}>
                    <Row>
                      {
                        prodLineList.map((item) => (
                          <Col span={12} key={item.id}><Checkbox value={item.id}>{item.abbreviation}</Checkbox></Col>
                        ))
                      }
                    </Row>
                  </CheckboxGroup>
                )}
              </FormItem>
            )
          }
          <FormItem label=''>
            {getFieldDecorator('departmentId', {
                rules: [{
                  required: true,
                  message: '',
                }],
                initialValue: isEdit ? editItem.deptId: undefined
              })(
                <Select placeholder="" style={{ width: 280 }}>
                  {
                    userDep.map((item) => (
                      <Option value={item.id} key={item.id}>{item.name}</Option>
                    ))
                  }
                </Select>
              )}
          </FormItem>
          <FormItem label=''>
            {
              !isEdit ? (getFieldDecorator('tel', {
                rules: [{
                  required: true,
                  message: '',
                },{
                  validator: this.validateTel
                }],
              })(
                <Input placeholder="" style={{ width: 280 }}/>
              )) : <Input placeholder="" value= {editItem.phone} style={{ width: 280 }} disabled={isEdit? true:false}/>
            }
          </FormItem>
          <FormItem label=''>
            {getFieldDecorator('name', {
                rules: [{
                  required: true,
                  message: '',
                }],
                initialValue: isEdit ? editItem.name: ''
              })(
                <Input placeholder="" style={{ width: 280 }}/>
            )}
          </FormItem>
          <FormItem label=''>
              {getFieldDecorator('email', {
                rules: [{
                  required: false,
                },{
                  validator: this.validateEmail
                }],
                initialValue: isEdit ? editItem.email: ''
              })(
                <Input placeholder="" style={{ width: 280 }}/>
              )}
          </FormItem>
          <div style={{textAlign: 'center', width: '60%', margin: '30px auto'}}>
            <Button type="primary" htmlType="submit" block disabled={disabled}></Button>
          </div>
        </Form>
      </Modal>
    )
  }
  showAddUserModal() {
    this.setState(() => ({userVisible: true, role: 'COORDINATOR'}))
  }
  render() {
    return (
    <div className="user_list_wrapper">
      <header className="header">
        <div className='title'>
          
          <label htmlFor="" className="add_user" onClick={this.showAddUserModal}><i className="iconfont icon-bj"></i></label>
        </div>
      </header>
      <main>
        <div className="filter_search">
          <Form
            layout='inline'
            className="ant-advanced-search-form"
          >
            <Row gutter={16}>
              <Col span={8}>
                <FormItem label=''>
                  <Select placeholder="" style={{ width: 240 }} onChange={this.roleChange} allowClear>
                    <Option value=""></Option>
                    <Option value="COORDINATOR"></Option>
                    <Option value="SERVANT"></Option>
                  </Select>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label=''>
                  <Select placeholder="" style={{ width: 240 }} onChange={this.depChange} allowClear>
                    <Option value=""></Option>
                    {
                      this.state.userDep.map((v, index) => (
                        <Option value={v.id} key={index}>{v.name}</Option>
                      ))
                    }
                  </Select>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label=''>
                  <Input placeholder="" style={{ width: 240 }} onChange={this.keywordsChange}/>
                </FormItem>
              </Col>
            </Row>
            <Row className="search_btn">
              <Col span={24} >
                  <FormItem label=''>
                    <Button type="primary" onClick={this.handleSearch} style={{width: '218px'}}></Button>
                </FormItem>
              </Col>
            </Row>
          </Form>
        </div>
        <div className="list_table">
          <Table
            {...tableState}
            columns={this.state.columns}
            pagination={ this.state.pagination }
            dataSource={this.state.employees}
          />
        </div>
      </main>
      {this.addUserRender(this.state.isEdit)}
    </div>)
  }
}

const WrappedUserList = Form.create()(UserList);
export default connect(null, null)(WrappedUserList)