import React, { Component } from 'react';
import { Form, Input, TreeSelect, Button, message } from 'antd'
import {openLoading, postAddProgram, getPhasesTree} from '/api'
import Upload from '@/components/common/upload'
const SHOW_PARENT = TreeSelect.SHOW_PARENT
const FormItem = Form.Item
class addForm extends Component{
  constructor (props) {
    super(props)
    this.state = {
      PhaseTree: [],
      fileList: []
    }
    this.addProgram = this.addProgram.bind(this)
    this.getChildrenFileList = this.getChildrenFileList.bind(this)
  }

  componentDidMount() {
    this.getPhasesTreeFuc()
  }

  getPhasesTreeFuc () {
    getPhasesTree().then((res) => {
      const datas = res.data.phases
      let tData = []
      datas.forEach((i, v) => {
        tData.push({title: i.name, value: i.defaultId, key: i.defaultId, children: [], disableCheckbox: true})
        i.nodes.forEach((x) => {
          tData[v].children.push({title: x.name, value: x.defaultId, key: x.defaultId, isLeaf: true})
        })
      })
      this.setState(() => ({
        PhaseTree: tData
      }))
    })
  }

  addProgram (e) {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let ids = []
        this.state.fileList.forEach((v) => {
          ids.push(v.id)
        })
        const obj = {
          ...values,
          attachments: ids
        }
        openLoading()
        this.props.programCancel() // 
        postAddProgram(obj).then((res) => {
          message.success('')
          this.props.getProgramFuc()
          this.props.form.resetFields()
          this.setState(() => ({fileList: []}))
          this.uploadComponent.resetUpload()
        })
      }
    });
  }

  getChildrenFileList (fileList) {
    this.setState(() => ({
      fileList
    }))
  }

  render() {
    const tProps = {
      treeData: this.state.PhaseTree,
      treeCheckable: true,
      disableCheckbox: true,
      showCheckedStrategy: SHOW_PARENT,
      searchPlaceholder: '',
      style: {
        width: 300,
      }
    }
    const { getFieldDecorator } = this.props.form
    return(
      <Form onSubmit={this.addProgram}>
        <FormItem label={''}>
          {
            getFieldDecorator('nodes', {
              rules: [{required: true, message: '!'}]
            })(
              <TreeSelect {...tProps} />
            )
          }
        </FormItem>
        <FormItem label={'    '}>
          {
            getFieldDecorator('keywords', {
              rules: [{required: true, message: '!'}],
            })(
              <Input.TextArea placeholder={''}/>
            )
          }
        </FormItem>
        <FormItem label={''}>
          {
            getFieldDecorator('content', {
              rules: [{required: true, message: '!'}]
            })(
              <Input.TextArea placeholder={''}/>
            )
          }
        </FormItem>
        <FormItem label={''}>
          <Upload
            fileList={this.state.fileList}
            getFileList={this.getChildrenFileList}
            ref={(upload) => this.uploadComponent = upload}/>
        </FormItem>
        <FormItem>
          <Button htmlType='submit'></Button>
        </FormItem>
      </Form>
    )
  }
}
export default Form.create()(addForm)

