import React from 'react'
import { Row, Col} from 'antd';
import selectedImg from '@/assets/img/selected.png'
import './projectInfo.scss'

const ProjectInfo = (props) => {
  const { projectDetail, contactCategory } = props
  return (
    <div className="detail_content_box">
        <p className="detail_content_title"></p>
          <ul className="detail_content_item">
            <li>{projectDetail.createdTime}</li>
            <li>{projectDetail.country}</li>
            <li>{projectDetail.phase}</li>
            <li>{projectDetail.proprietor}</li>
            <li>{projectDetail.designInstitute}</li>
            <li>{projectDetail.completePlant ? projectDetail.completePlant : '--'}</li>
            <li>{projectDetail.generalContractor ? projectDetail.generalContractor : '--'}</li>
            <li>{projectDetail.investor}</li>
            <li>{projectDetail.estimatedTenderTime}</li>
            <li>{projectDetail.estimatedContractTime}</li>
            <li>{projectDetail.estimatedSupplyTime}</li>
          </ul>
          <p className="detail_content_title"></p>
          <ul className="detail_content_item">
            <li>{projectDetail.productOrientation}</li>
            <li>{projectDetail.productCategory}</li>
            <li>{projectDetail.productQuantity}</li>
          </ul>
          <p className="detail_content_title"></p>
          <Row gutter={16} className="resource-category detail_content_item">
            {
              contactCategory.map((v, index) => (
                <Col span={8} key={index}>
                  <div className='resource_item_box'>
                    {
                      v.isSelect ? <img alt="" src={selectedImg} className="selected"/> : <label className="not-select"></label>
                    }
                    <label htmlFor="" className="resource_item">{v.title}</label>
                  </div>
                </Col>
              ))
            }
          </Row>
          <p className="detail_content_title"></p>
          <ul className="detail_content_item">
            <li>{projectDetail.contact}</li>
            <li>{projectDetail.contactTel}</li>
            <li>{projectDetail.contactProvince?`${projectDetail.contactProvince}${projectDetail.contactCity}${projectDetail.contactDistrict}`:''}</li>
            <li>{projectDetail.contactEmail === ''?'':projectDetail.contactEmail}</li>
          </ul>
      </div>
  )
}

export default ProjectInfo
