import React, { Component } from 'react';
import { Table, Form, Row, Col, Input, Button, Select  } from 'antd';
import { connect } from 'react-redux';
import { getProjectList, openLoading, closeLoading } from '@/utils/api'
import countryData from '@/utils/util/countryData'
import { formDate } from '@/utils/util/common'
import './index.scss';
import Region from '@/components/project/region'

const FormItem = Form.Item;
const Option = Select.Option;
// 
let country = []
countryData.map(v => country.push(...v.data))
// 
const tableState = {
  bordered: true,
  pagination: true,
  showHeader: true,
  size: "middle",
  rowKey: "id"
}
class Report extends Component {
  constructor(props) {
    super(props)
    this.state = {
      projectList: null,
      isChina: false,
      params: {
        page: 0,
        size: 10
      },
      pagination: { // 
        total: 0,
        showSizeChanger: true,
        showQuickJumper: true,
        onChange: (page, pageSize) => this.pageChange(page, pageSize),
        onShowSizeChange: (current, size) => this.sizeChange(current, size)
      }
    }
    this.selectData = {
      status: '',
      country: '',
      province: '',
      city: '',
      district:'',
      createdTimeBegin: '',
      keywords: ''
    }
    // 
    this.projectStatus = {
      CREATED:'',
      IN_REVIEW:'',
      PASSED:'',
      ANALYZED:'',
      NOT_PASSED:'',
      COMPLETED:'',
      CLOSED:''
    }
    this.columns = [
      {
        dataIndex: 'name',
        title: '',
      },
      {
        dataIndex: 'region',
        title: '',
      },
      {
        dataIndex: 'phase',
        title: '',
      },
      {
        dataIndex: 'product',
        title: '',
      },
      {
        dataIndex: 'createdTime',
        title: '',
      },
      {
        dataIndex: 'status',
        title: '',
      },
      {
        dataIndex: 'progress',
        title: '',
      },
      {
        title: '',
        dataIndex: 'id',
        render: (text) => {
          return <span onClick={ this.toDetail.bind(this, text) } style={{color: 'rgba(44,112,231,1)'}}></span>
        },
      },
    ];
    this.handleSearch = this.handleSearch.bind(this)
    this.countryChange = this.countryChange.bind(this)
    this.statusChange = this.statusChange.bind(this)
    this.regionChange = this.regionChange.bind(this)
    this.dateChange = this.dateChange.bind(this)
    this.keywordsChange = this.keywordsChange.bind(this)
  }
  componentDidMount() {
    this.queryProjectList()
  }
  // 
  keywordsChange(e) {
    this.selectData.keywords = e.target.value
  }
   // 
  toDetail(id) {
    this.props.history.push(`/home/project/detail/${id}`)
  }
  // 
  pageChange(page, pageSize) {
    let params = {
      size: pageSize,
      page: page -1
    }
    this.queryProjectList(params)
  }
  sizeChange(current, size) {
    let params = {
      size,
      page: current - 1
    }
    let datas = Object.assign({}, this.state.params, { size: size })
    this.setState(() => {
      return {params: datas}
    })
    this.queryProjectList(params)
  }
  // 
  queryProjectList(params = {page: 0, size: 10}) {
    openLoading()
    let datas = {
      paging: true,
      ...this.selectData,
      ...params
    }
    getProjectList(datas).then(res => {
      closeLoading()
      let { content, total} = res.data
      let pagination = Object.assign({}, this.state.pagination)
      pagination.total = total
      let phase = {APPROVAL: '', DESIGN: '', TENDERING: ''}
      content.forEach(v => {
        v.status = this.projectStatus[v.status]
        v.phase = phase[v.phase]
        v.createdTime = formDate(v.createdTime)
      })
      this.setState(() => {
        return { projectList: content, pagination }
      })
    })
  }
  handleSearch() {
    this.queryProjectList(this.state.params)
  }
  // 
  countryChange(val) {
    if (val === '') {
      this.setState(() => {
        return {isChina: true}
      })
    } else {
      this.setState(() => {
        return {isChina: false}
      })
    }
    this.selectData = Object.assign({}, this.selectData, {country: val, province: '',city: '',district:'',} )
  }
  // 
  dateChange(item) {
    this.selectData.createdTimeBegin = item ? this.date(item) : '';
  }
  // 
  date (str) {
    let date = new Date()
    let old = ''
    switch (str) {
      case '1': old = date.setDate(date.getDate() - 1)
        break
      case '1': old = date.setDate(date.getDate() - 7)
        break
      case '1': old = date.setMonth(date.getMonth() - 1)
        break
      case '1': old = date.setMonth(date.getMonth() - 3)
        break
      case '': old = date.setMonth(date.getMonth() - 6)
        break
      default:
        old = date.setFullYear(date.getFullYear() - 1)
    }
    return old
  }
  // 
  statusChange(val) {
    this.selectData.status = val ? val : '';
  }
  // 
  regionChange (item) {
    let [province, city, district] = item ? item : {}
    this.selectData = Object.assign({}, this.selectData, { province, city, district })
  }
  render() {
    return (
    <div className="report_list_wrapper">
      <header className="header">
        
        <label htmlFor="" className="right_btn"><i className="iconfont icon-bj"></i></label>
      </header>
      <main>
        <div className="filter_search">
          <Form
            layout='inline'
            className="ant-advanced-search-form"
          >
            <Row gutter={16}>
              
              <Col span={8}>
                <FormItem label=''>
                  <Select placeholder="" style={{ width: 240 }} onChange={this.statusChange} allowClear>
                    <Option value=""></Option>
                    <Option value="CREATED"></Option>
                    <Option value="IN_REVIEW"></Option>
                    <Option value="PASSED"></Option>
                    <Option value="ANALYZED"></Option>
                    <Option value="NOT_PASSED"></Option>
                    <Option value="CLOSED"></Option>
                    <Option value="COMPLETED"></Option>
                  </Select>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label=''>
                  <Input placeholder="" style={{ width: 240 }} onChange={this.keywordsChange}/>
                </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label=''>
                    <Select placeholder="" style={{ width: 240 }} onChange={this.dateChange} allowClear>
                      <Option value="1">1</Option>
                      <Option value="1">1</Option>
                      <Option value="1">1</Option>
                      <Option value="1">1</Option>
                      <Option value=""></Option>
                      <Option value="1">1</Option>
                    </Select>
                  </FormItem>
              </Col>
              <Col span={8}>
                <FormItem label=''>
                  <Select placeholder="" style={{ width: 240 }} onChange={this.countryChange} allowClear>
                    {
                      country.map(v => <Option value={v} key={v}>{v}</Option>)
                    }
                  </Select>
                </FormItem>
              </Col>
              {
                this.state.isChina ? (<Col span={8}>
                  <FormItem label=''>
                    <Region regionChange={this.regionChange}></Region>
                  </FormItem>
                </Col>): ''
              }
              
            </Row>
            <Row>
              <Col span={24} style={{ textAlign: 'left' }}>
                <Button type="primary" onClick={this.handleSearch} style={{width: '218px', margin: '20px 0 0 0'}}></Button>
              </Col>
            </Row>
          </Form>
        </div>
        <div className="list_table">
          <Table
            {...tableState}
            columns={this.columns}
            pagination={ this.state.pagination }
            dataSource={this.state.projectList}
          />
        </div>
      </main>
    </div>)
  }
}
export default connect(null, null)(Report)