import React, { Component } from 'react';
import { Button , Row, Col, Modal, Input, message } from 'antd';
import { getProjectDetail,
  getProjectFollowUps,
  getProjecTask,
  postProjectInspcet,
  PostProjectFollowUps,
  postCloseProject,
  transferProject,
  getUserlist,
  openLoading,
  closeLoading,
  delPeojectFollowUp,
 } from '@/utils/api'
import { formdateTime } from '@/utils/util/common'

import ProjectTask from '@/components/project/taskList/taskList'
import ProjectInfo from '@/components/project/projectInfo/projectInfo'
import StagePreset from '@/components/project/stagePreset/stagePreset'
import TransferProject from '@/components/project/transferProject/transferProject'

import './detail.scss';

const confirm = Modal.confirm;
const { TextArea } = Input;

// 
function followUpRecord(data={}, page = 0, _this) {
  const datas = page === 0 ? data.slice(0, 3) : data
  return (
    <div className="record_item_out">
      {
        datas.map((item,index) => (
        <div className="record_item" key={index}>
          <div className="record_item_title">{item.content}</div>
          <div className="record_item_person">
            <label htmlFor="" className="user_name">{item.userName}</label>
            <label htmlFor="" className="follow_date">{formdateTime(item.createdTime)}</label>
            {
              item.userId === sessionStorage.userId ? (<label htmlFor="" className="del_follow" onClick={() => {_this.delFollowUp(item.id)}}></label>): ''
            }
          </div>
        </div>
      ))}
    </div>
  )
}

class Project extends Component {
  constructor(props) {
    super(props)
    this.state = {
      loading: false,
      contactCategory: [
        { title: '', isSelect: false },
        { title: '', isSelect: false },
        { title: '/', isSelect: false },
        { title: '', isSelect: false },
        { title: '', isSelect: false },
        { title: '', isSelect: false },
        { title: '', isSelect: false },
        { title: '', isSelect: false },
        { title: '', isSelect: false },
        { title: '', isSelect: false }
      ], // 
      projectDetail: {}, // 
      reviewPage: 0, // 
      reviewList: [], //
      loadingComplete: false, // 
      taskList: [], // 
      visible: false, //
      followVisible: false, //
      reason: '', // 
      followReason: '', //
      stopReasonValue: '', // 
      stagePresetModal: false, //  
      transferModal: false, //  
      userList: [], // 
    }
    this.projectId = this.props.match.params.projectId
    // 
    this.loadingMore = this.loadingMore.bind(this)
    this.operating = this.operating.bind(this)
    this.handleCancel = this.handleCancel.bind(this)
    this.handleConfirm = this.handleConfirm.bind(this)
    this.areaChange = this.areaChange.bind(this)
    this.submitReview = this.submitReview.bind(this)
    // 
    this.followHandChange = this.followHandChange.bind(this)
    this.showFollowArea = this.showFollowArea.bind(this)
    this.followHandleCancel = this.followHandleCancel.bind(this)
    this.followHandleConfirm = this.followHandleConfirm.bind(this)
    // 
    this.delFollowUp = this.delFollowUp.bind(this)
    
    // 
    this.stopReason = this.stopReason.bind(this)
    this.stopTeamwork = this.stopTeamwork.bind(this)
    // 
    this.stagePreset = this.stagePreset.bind(this)
    this.closeStagePresetModal = this.closeStagePresetModal.bind(this)
    // 
    this.transferProject = this.transferProject.bind(this)
    this.closeTransferModle = this.closeTransferModle.bind(this)
    
    // 
    this.queryAllTask = this.queryAllTask.bind(this)
    this.addTask = this.addTask.bind(this)

  }
  componentDidMount() {
    this.queryProjectDetail()
    this.queryFollow({size: 10, page: 0})
    this.queryTaskList()
    this.queryUserlist('COORDINATOR')
  }
  // 
  oprateRender(status) {
    let waitCheck = ['CREATED', 'NOT_PASSED', 'IN_REVIEW']
    if (waitCheck.indexOf(status) > -1) {
      return (<div>
        <Button className="review" onClick={() => {this.operating(0)}}><i className="iconfont icon-shiliangzhinengduixiang9"></i></Button>
        <Button className="review" onClick={() => {this.operating(1)}}><i className="iconfont icon-shiliangzhinengduixiang10"></i></Button>
      </div>)
    }
    if (status === 'CLOSED') {
      return ('')
    }
    return (<div>
      <Button className="review" onClick={this.stagePreset}><i className="iconfont icon-shiliangzhinengduixiang6"></i></Button>
      <Button className="review" onClick={this.transferProject}><i className="iconfont icon-shiliangzhinengduixiang8"></i></Button>
      <Button className="review" onClick={this.stopTeamwork}><i className="iconfont icon-shiliangzhinengduixiang7"></i></Button>
    </div>)
  }
  // 
  taskListRender(status) {
    let waitCheck = ['CREATED', 'NOT_PASSED', 'IN_REVIEW']
    if (waitCheck.indexOf(status) > -1) return
    return (
      <div className="detail_task_list">
        <p className="right_title"><label htmlFor="" onClick={this.queryAllTask} className="task_right_btn"></label><label htmlFor="" onClick={this.addTask} className="task_right_btn"></label></p>
        <div className="detail_task_list_box">
          {
            this.state.taskList.map((item, index) => (<ProjectTask projectId={this.projectId} taskList={item} key={index} history={this.props.history}></ProjectTask>))
          }
        </div>
      </div>
    )
  }
  // 
  queryProjectDetail() {
    openLoading()
    getProjectDetail(this.projectId).then(res => {
      closeLoading()
      if (!res || !res.data) return
      let datas = res ? res.data : {}
      this.setState(() => ({projectDetail: datas}))
      // 
      let resources = datas && res.data.resources
      if (resources && resources.length !== 0) {
        let contactCategory = this.state.contactCategory
        contactCategory.forEach(v => {
          if (resources.includes(v.title)) {
            v.isSelect = true
          }
        })
        this.setState(() => ({contactCategory}))
      }
    })
  }
  
  // 
  queryUserlist (role) {
    let obj = {}
    if (role) {
      obj.roles = role
    }
    getUserlist(obj).then(res => {
      let datas = res.data
      // 
      datas.forEach(v => {
        v.employees = v.employees.filter((item) => sessionStorage.userId !== item.userId)
      })
      this.setState(() => ({
        userList: datas
      }))
    })
  }
  // 
  transferProject() {
    let transferModal = !this.state.transferModal
    this.setState(() => ({ transferModal }))
  }
  // 
  closeTransferModle() {
    this.setState(() => ({ transferModal: false }))
  }
  // 
  confirmSubmit(selectedId, history) {
    openLoading()
    const _this = this
    if (selectedId.length === 0) {
      message.warning('', 1)
      return
    }
    transferProject(this.projectId, {target:selectedId[0]}).then((res) => {
      closeLoading()
      if (res.status === 200) {
        message.info('', 1)
        _this.closeTransferModle()
        history.replace('/home/project/list')
      }
    })
  }
  // 
  stagePreset() {
    let stagePresetModal = !this.state.stagePresetModal
    this.setState(() => ({ stagePresetModal }))
  }
  // 
  closeStagePresetModal(state = false) {
    if (state) this.queryProjectDetail()
    this.setState(() => ({ stagePresetModal: false }))
  }
  loadingMore() {
    let patams = {
      size: 10,
      page: this.state.reviewPage + 1
    }
    this.queryFollow(patams)
    this.setState(() => ({ loading: true, reviewPage: this.state.reviewPage + 1}))
  }
  
  // 
  queryFollow(params={page:0,size:10}) {
    getProjectFollowUps(this.projectId, params).then((res) => {
      if (!res || !res.data) return
      const datas = res.data
      if (datas.length < 4) {
        this.setState(() => ({loadingComplete: false}))
      } else {
        this.setState(() => ({loadingComplete: true}))
      }
      if (params.page === 0) {
        this.setState(() => ({reviewList: datas, loading: false}))
        return
      }
      let reviewList =this.state.reviewList.concat(datas)
      this.setState(() => ({reviewList, loading: false}))
    })
  }
  // 
  delFollowUp(id) {
    confirm({
      title: '?',
      okText: '',
      cancelText: '',
      onOk: () => {
        this.delFollowFunc(id)
      }
    });
  }
  delFollowFunc(id) {
    openLoading()
    delPeojectFollowUp(this.projectId, id).then((res) => {
      closeLoading()
      if (res.status === 200) {
        message.info('', 1)
        let reviewList = this.state.reviewList
        reviewList = reviewList.filter(v => v.id !== id)
        this.setState(() => ({reviewList}))
      }
    })
  }
  // 
  queryTaskList() {
    let obj = {
      page: 0,
      size: 3
    }
    getProjecTask(this.projectId, obj).then((res) => {
      this.setState(() => ({taskList: res.data}))
    });
  }
  // 
  areaChange(e) {
    let reason = e.target.value.replace(/(^\s*)|(\s*$)/g, "")
    this.setState(() => ({
      reason
    }))
  }
  // 
  submitReview (passed, reason) {
    openLoading()
    let obj = {
      passed
    }
    if (reason) {
      obj.reason = reason
    }
    postProjectInspcet(this.projectId, obj).then(res => {
      closeLoading()
      if (res.status === 200) {
        this.setState(() => ({
          reason: '',
          visible: false
        }))
        message.info('', 1)
        this.queryProjectDetail()
        this.queryFollow()
      }
    })
  }
  // 
  handleCancel() {
    this.setState({
      visible: false
    });
  }
  // 
  handleConfirm() {
    let failReason = this.state.reason.replace(/(^\s*)|(\s*$)/g, "")
    if (failReason === '') {
      message.warning('', 1)
      return
    }
    this.submitReview(false, failReason)
  }
  // 
  // 
  followHandleCancel() {
    this.setState({
      followVisible: false,
      followReason: ''
    });
  }
  // 
  showFollowArea() {
    this.setState(() => ({followVisible: true}))
  }
  // 
  addFollowReview() {
    openLoading()
    PostProjectFollowUps(this.projectId, {content: this.state.followReason}).then((res) => {
      closeLoading()
      if (res.status === 200) {
        this.setState(() => ({followReason: '', followVisible: false, reviewPage: 0}))
        message.info('', 1)
        this.queryFollow({page: 0, size: 10})
      }
    })
  }
  // 
  followHandleConfirm() {
    let followReason = this.state.followReason
    if (followReason === '') {
      message.warning('', 1)
      return
    }
    this.addFollowReview()
  }
  followHandChange(e) {
    let followReason = e.target.value.replace(/(^\s*)|(\s*$)/g, "")
    this.setState(() => ({
      followReason
    }))
  }
  // 
  stopTeamwork() {
    const _this = this
    confirm({
      title: '?',
      content: (<div>
        <textarea name="" id="" rows="6" style={{width: '100%', marginTop: '20px'}} onChange={this.stopReason} className="text_area"></textarea>
      </div>),
      onOk() {
        let stopReasonValue = _this.state.stopReasonValue.replace(/(^\s*)|(\s*$)/g, "")
        if (stopReasonValue === '') {
          message.warning('', 1)
          return !0
        }
        postCloseProject(_this.projectId, {reason: _this.state.stopReasonValue}).then(res => {
          if (res.status === 200) {
            message.info('', 1)
          }
        })
      },
      onCancel() {
        console.log('Cancel');
      },
    });
  }
  stopReason(e) {
    let reason = e.target.value
    this.setState(() => ({
      stopReasonValue: reason
    }))
  }
  // 
  queryAllTask() {
    this.props.history.push(`/home/project/projectTaskList/${this.projectId}/${this.state.projectDetail.name}`)
  }
  // 
  operating (i) {
    switch(i) {
      case 0:
        confirm({
          title: '?',
          okText: '',
          cancelText: '',
          onOk: () => {
            this.submitReview(true)
          }
        });
        break;
      case 1:
        this.setState({
          visible: true,
        });
        break;
      default:
    }
  }
  // 
  addTask () {
    let { projectDetail } = this.state
    let statusArr = ['NOT_PASSED', 'CLOSED', 'COMPLETED']
    if (statusArr.indexOf(projectDetail.status) > -1) {
      message.warning('');
      return
    }
    if ( projectDetail.status !== 'ANALYZED') {
      message.warning('');
      return
    }
    this.props.history.push(`/home/project/addTask/${this.projectId}`)
  }

  render() {
    const {projectDetail, contactCategory, loadingComplete, loading, reviewList, stagePresetModal, transferModal, userList} = this.state
    return (
      <div className="project_detail_wrapper">
        <div className="project_detail_innner">
          <header className='project_detail_header'>
            <Row justify="space-between">
              <Col span={6}>{projectDetail.name}</Col>
              <Col span={18}>
                <div className="review_box">
                  {
                    projectDetail.status ? this.oprateRender(projectDetail.status): ''
                  }
                </div>
                <div>
                  {/*  */}
                  <Modal title=''
                    visible={this.state.visible}
                    onOk={this.handleConfirm}
                    onCancel={this.handleCancel}
                  >
                  <TextArea rows={4} onChange={this.areaChange} className="text_area"/>
                </Modal>
                </div>
              </Col>
            </Row>
          </header>
          <main className='detail_content'>
            <Row gutter={20}>
              <Col span={10}>
                <ProjectInfo projectDetail={projectDetail} contactCategory={contactCategory}></ProjectInfo>
              </Col>
              <Col span={14}>
                <div className="record">
                  <p className="right_title">
                    
                    <label htmlFor="" className="add_follow" onClick={this.showFollowArea}><i className="iconfont icon-jia"></i></label>
                  </p>
                  <div>
                      <Modal title=''
                        visible={this.state.followVisible}
                        onOk={this.followHandleConfirm}
                        onCancel={this.followHandleCancel}
                      >
                        <TextArea rows={4} onChange={this.followHandChange} value={this.state.followReason} className="text_area"/>
                      </Modal>
                    </div>
                  <div>
                  </div>
                  {followUpRecord(reviewList, this.state.reviewPage, this)}
                  {
                    loadingComplete ? (<div className="record_loading_more">
                      <Button type="primary" loading={loading} onClick={this.loadingMore}>
                        
                      </Button>
                    </div>) : ''
                  }

                </div>
                {projectDetail.status ?this.taskListRender(projectDetail.status) : ''}
              </Col>
            </Row>
          </main>
          {/*  */}
          <StagePreset projectId={this.projectId} stagePresetModal={stagePresetModal} closeStagePresetModal={this.closeStagePresetModal}></StagePreset>
          {/*  */}
          <TransferProject history={this.props.history} confirmSubmit={this.confirmSubmit} userList={userList} projectId={this.projectId} transferModal={transferModal} closeTransferModle={this.closeTransferModle}></TransferProject>
        </div>
      </div>
    )
  }
}
export default Project;
